import React, { useState } from 'react';
import {
  Button,
  FormControl,
  FormLabel,
  Input,
  ModalBody,
  ModalFooter
} from '@chakra-ui/react';
import { IJobCategoryTable } from '../../../../interfaces/Itable';
import api from '../../../../utils/api';

interface EditPortalCategoryProps {
  onClose: () => void;
  item: IJobCategoryTable;
}

const EditJobCat: React.FC<EditPortalCategoryProps> = ({ onClose, item }: EditPortalCategoryProps) => {
  const [formData, setFormData] = useState({
    jobName: item.jobName || '', // Assuming item contains the job name
    desc:item.desc || '', // Placeholder for portal link - please replace with actual property
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleEdit = async() => {
    console.log('Edited job category:', formData);
    try {
      const res = await api.patch(`/portal/update_category/${item._id}`, formData);
      if (res.data) {
        window.location.reload();
      }
    } catch (err) {
      console.log(err);
    }
    onClose();
  };

  return (
    <>
      <ModalBody pb={6}>
        <FormControl>
          <FormLabel>Job Category name</FormLabel>
          <Input
            placeholder="Job Category Name"
            name="jobName"
            value={formData.jobName}
            onChange={handleChange}
          />
        </FormControl>

        <FormControl mt={4}>
          <FormLabel>Portal Link</FormLabel>
          <Input
            placeholder="Portal Link"
            name="desc"
            value={formData.desc}
            onChange={handleChange}
          />
        </FormControl>
      </ModalBody>

      <ModalFooter>
        <Button colorScheme="blue" mr={3} onClick={handleEdit}>
          Edit
        </Button>
        <Button onClick={onClose}>Cancel</Button>
      </ModalFooter>
    </>
  );
};

export default EditJobCat;
