import React, { useState, useEffect } from 'react';
import { Flex, Heading, Input } from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import Button from '../components/UI/button/Button';
import LoadingSpinner from '../components/UI/loadingSpinner/LoadingSpinner';
import CustomTable from '../components/tables/customTable/CustomTable';
import { IProposalTable } from '../interfaces/Itable';
import { customersHeader } from '../constants/tables';
import useDataFetch from '../hook/useDataFetch';

const Proposal = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const portalProposalUrl = `${process.env.REACT_APP_API_URL}/api/portal/proposals`;
  const { data, error, loading } = useDataFetch<IProposalTable[]>(portalProposalUrl);
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredData, setFilteredData] = useState<IProposalTable[]>([]);

  useEffect(() => {
    if (data) {
      const filtered = data.filter(
        (item) =>
          item.profile.profileName.toLowerCase().includes(searchTerm.toLowerCase()) ||
          item.creator.name.toLowerCase().includes(searchTerm.toLowerCase())
      );
      setFilteredData(filtered);
    }
  }, [data, searchTerm]);

  if (loading) {
    return <LoadingSpinner />;
  }

  if (error) {
    return <p>Error loading proposals</p>; // Handle the error more appropriately
  }
  return (
    <>
      <section>
        <Flex mb={4} alignItems="center" justify="space-between">
          <Heading color="red">{t('proposals')}</Heading>
          <Button outline onClick={() => navigate('/proposals/create')}>
            {t('createProposal')}
          </Button>     
        </Flex>
        <Input
          type="text"
          w={'fit-content'}
          placeholder="Search by profile or username"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        {filteredData.length > 0 ? (
          <CustomTable key={Date.now()} headData={customersHeader} bodyData={filteredData} limit={10} />
        ) : (
          <p>No matching proposals found</p>
        )}
      </section>
    </>
  );
};

export default Proposal;
