import {
    Button,
    Modal,
    ModalCloseButton,
    ModalContent,
    ModalFooter,
    ModalHeader,
    ModalOverlay,
    useDisclosure
} from '@chakra-ui/react';
import React from 'react';
import CreatePortal from '../create/createPortal/CreatePortal';
import CreatePortalProfile from '../create/createProfile/CreatePortalProfile';
import CreateJobCat from '../create/createJobCategory/CreateJobCat';
import { IJobCategoryTable, IPortalTable } from '../../interfaces/Itable';
import CreateClient from '../create/createClient/CreateClient';
interface ICreateModalProps {
  button: string;
  header: string;
  num: number;
  portal?: IPortalTable[];
  jobCat?: IJobCategoryTable[];
}
  export default function CreateModal({button,portal,jobCat, header,num }: ICreateModalProps) {
    const { isOpen, onOpen, onClose } = useDisclosure();
    const initialRef = React.useRef<HTMLInputElement>(null);
  
    return (
      <>
        <Button onClick={onOpen}>{button}</Button>
  
        <Modal initialFocusRef={initialRef} isOpen={isOpen} onClose={onClose}>
          <ModalOverlay />
          <ModalContent>
            <ModalHeader>{header}</ModalHeader>
            <ModalCloseButton />
   {num===0?  <CreatePortal onClose={onClose}/>: num===1 ? <CreatePortalProfile portal={portal} jobCat={jobCat} onClose={onClose}/> :  num===10 ? <CreateClient onClose={onClose}/> : <CreateJobCat onClose={onClose}/>}       
          </ModalContent>
        </Modal>
      </>
    );
  }
  