import { useTranslation } from "react-i18next";
import LoadingSpinner from "../components/UI/loadingSpinner/LoadingSpinner";
import CustomTable from "../components/tables/customTable/CustomTable";
import { customers, usersHeader } from "../constants/tables";
import useDataFetch from "../hook/useDataFetch";
import { IusersTable } from "../interfaces/Itable";
const url = `${process.env.REACT_APP_API_URL}/api/users`;
function Users() {
  const { t } = useTranslation();
  const { data, error, loading } = useDataFetch<IusersTable[]>(url);
// console.log(data)
  let userTable;

  if (loading) {
    userTable = <LoadingSpinner />;
  }

  if (error) {
    userTable = (
      <CustomTable limit={10} headData={usersHeader} bodyData={[]} />
    );
  }

  if (!loading && data) {
    userTable = (
      <CustomTable limit={10} headData={usersHeader} bodyData={data} />
    );
  }

  return (
    <section>
      <h2 className="title">{t("users")}</h2>
      {userTable}
    </section>
  );
}

export default Users;
