import { useState, ChangeEvent } from 'react';
import {
  FormControl,
  FormLabel,
  Input,
  ModalBody,
  ModalFooter,
  Button
} from '@chakra-ui/react';
import api from '../../../utils/api';

interface PortalData {
  portalName: string;
  link: string;
}

const CreatePortal = ({ onClose }: { onClose: () => void }) => {
  const initialPortalData: PortalData = {
    portalName: "",
    link: ""
  };

  const [portalData, setPortalData] = useState<PortalData>(initialPortalData);

  const handleChange = (e: ChangeEvent<HTMLInputElement>): void => {
    const { name, value } = e.target;
    setPortalData({ ...portalData, [name]: value });
  };
  const handleCreate = async () => {
    console.log("Create clicked, data:", portalData);
    try {
      const res = await api.post(`/portal/create`, portalData);
      if (res.data)
        window.location.reload();
    } catch (err) {
      console.log(err)
    }
    onClose();
  };

  return (
    <>
      <ModalBody pb={6}>
        <FormControl>
          <FormLabel>Portal name</FormLabel>
          <Input
            name='portalName'
            value={portalData.portalName}
            onChange={handleChange}
            placeholder="Upwork, Fiverr"
          />
        </FormControl>

        <FormControl mt={4}>
          <FormLabel>Portal Link</FormLabel>
          <Input
            name='link'
            value={portalData.link}
            onChange={handleChange}
            placeholder="https://...."
          />
        </FormControl>
      </ModalBody>

      <ModalFooter>
        <Button colorScheme="blue" mr={3} onClick={handleCreate}>
          Create
        </Button>
        <Button onClick={onClose}>Cancel</Button>
      </ModalFooter>
    </>
  );
};

export default CreatePortal;
