import React, { useEffect, useState } from 'react';
import { Button, FormControl, FormLabel, Input, ModalBody, ModalFooter, Select as Shivam } from '@chakra-ui/react';
import { IPortalProfileTable, IusersTable } from '../../../../interfaces/Itable';
import { GroupBase, OptionsOrGroups, Select } from "chakra-react-select";
import useDataFetch from '../../../../hook/useDataFetch';
import api from '../../../../utils/api';

const EditUser = ({ onClose, item }: { onClose: () => void; item: IusersTable }) => {
  const [editedItem, setEditedItem] = useState<IusersTable>(item);
  const [selected, setSelected] = useState<{
    value: string;
    label: string;
  }[]>(item.profilesAllocated.map((profile: any) => ({
    value: profile._id,
    label: profile.profileName,
  })));
  
  useEffect(()=>{
setSelected(item.profilesAllocated.map((profile: any) => ({
  value: profile._id,
  label: profile.profileName,
})))
  },[item.profilesAllocated])
  
  const PortalProfileUrl=`${process.env.REACT_APP_API_URL}/api/portal/profile`
  const { data,  loading } = useDataFetch<IPortalProfileTable[]>(PortalProfileUrl);
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setEditedItem({
      ...editedItem,
      [name]: value,
    });
  };

  const handleEdit = async() => {
    try {
      const res = await api.put(`/users/${item._id}`, editedItem);
      if (res.data) {
        window.location.reload();
      }
    } catch (err) {
      console.log(err);
    }
    onClose();
  };
  const handleMulti = (value: { value: string; label: string }[]) => {
    const selectedIds = value.map((option) => option.value);
    setSelected(value);
    setEditedItem({ ...editedItem, profilesAllocated: selectedIds });
  };
  
  return (
    <>
      <ModalBody pb={6}>
        <FormControl>
          <FormLabel>Name</FormLabel>
          <Input
            name="name"
            value={editedItem.name}
            onChange={handleChange}
            readOnly
          />
        </FormControl>

        <FormControl mt={4}>
          <FormLabel>Target</FormLabel>
          <Input
            name="target"
            value={editedItem.target || ''}
            onChange={handleChange}
          />
        </FormControl>

        <FormControl mt={4}>
          <FormLabel>Role</FormLabel>
          <Shivam
            placeholder='Select option'
            name='jobCategory'
            value={editedItem.role}
            onChange={handleChange}
          >
            {["admin","member","manager"].map((el) => (
              <option key={el} value={el}>
                {el}
              </option>
            ))}
          </Shivam>
        </FormControl>

        {data && (
     <FormControl mt={4}>
     <FormLabel>Profile Allocated</FormLabel>
     <Select
       isMulti
       name="jobCategoryId"
       onChange={(value) => handleMulti(value as any)}
       value={selected}
       colorScheme="purple"
       options={data?.map((el) => ({
         value: el._id,
         label: el.profileName,
       })) as any}
       placeholder="Select some categories..."
       closeMenuOnSelect={false}
     />
   </FormControl>
        )}
      </ModalBody>

      <ModalFooter>
        <Button colorScheme="blue" mr={3} onClick={handleEdit}>
          Edit
        </Button>
        <Button onClick={onClose}>Cancel</Button>
      </ModalFooter>
    </>
  );
};

export default EditUser;