import { useState } from "react";
import { useTranslation } from "react-i18next";
import useDataFetch from "../../hook/useDataFetch";
import { IJobCategoryTable, IPortalProfileTable, IPortalTable } from "../../interfaces/Itable";
import CreateModal from "../modal/CreateModal";
import CustomTable from "../tables/customTable/CustomTable";
import "./Portal.scss";
import LoadingSpinner from "../UI/loadingSpinner/LoadingSpinner";


function Portal() {
    const PortalUrl=`${process.env.REACT_APP_API_URL}/api/portal`
    const PortalPorfileUrl=`${process.env.REACT_APP_API_URL}/api/portal/profile`
    const PortalCategoryUrl=`${process.env.REACT_APP_API_URL}/api/portal/category`
    const { t } = useTranslation();
    const [selected, setSelected] = useState();
    const { data, error, loading } = useDataFetch<IPortalTable[]>(PortalUrl);
    const { data:profileData, error:profileError, loading:profileLoading } = useDataFetch<IPortalProfileTable[]>(PortalPorfileUrl);
    const { data:categoryData, error:categoryError, loading:categoryLoading } = useDataFetch<IJobCategoryTable[]>(PortalCategoryUrl);
    let portalProfileTable;
    let portalTable;
    let categoryTable;
    //   let tableData: IProductsTable[] | undefined;

    if (loading) {
        portalTable = <LoadingSpinner />;
      }
    if (profileLoading) {
        portalProfileTable = <LoadingSpinner />;
      }
    if (categoryLoading) {
        categoryTable = <LoadingSpinner />;
      }
    if (error) {
        portalTable = (
            <CustomTable headData={[
                "ID",
                "Portal Name",
                "Link", "Actions"
            ]} bodyData={[]} limit={6} />)
      }
    if (profileError) {
        portalProfileTable = (
            <CustomTable headData={[
                "_id",
                "Profile Name",
                "Portal",
                "Job Category",
                "Profile Link", "Actions"
            ]} bodyData={[]} limit={6} />
        );
      }
      if (categoryError) {
        categoryTable = (
            <CustomTable headData={[
                "_id",
                "Job Category",
                "Description", "Actions"
            ]} bodyData={[]} limit={6} />
        );
      }
      if (!loading && data) {
          portalTable = (
        <CustomTable headData={[
            "ID",
            "Portal Name",
            "Link", "Actions"
        ]} bodyData={data} limit={6} />
    );
      }

      if (!loading && profileData) {
        portalProfileTable = (
            <CustomTable headData={[
                "_id",
                "Profile Name",
                "Portal",
                "Job Category",
                "Profile Link", "Actions"
            ]} bodyData={profileData} limit={6} />
        );
      }

  
      if (!categoryLoading && categoryData) {
    categoryTable = (
        <CustomTable headData={[
            "_id",
            "Job Category",
            "Description", "Actions"
        ]} bodyData={categoryData} limit={6} />
    );

    }

    return (
        <section>
            <div className="portal_creation">
                <h2 className="title">{t("Portal")}</h2>
           
                <CreateModal button={"Create Portal"} header={"Create portal"} num={0} /> </div>
                {portalTable}
            <div className="portal_creation">
                <h2 className="title">{t("Portal Profile")}</h2>
                <CreateModal portal={data || undefined} jobCat={categoryData || undefined} button={"Create Portal Profile"} header={"Create portal Profile"} num={1} /> </div>
                {portalProfileTable}
           
            
            <div className="portal_creation">
                <h2 className="title">{t("Job Category")}</h2>
                <CreateModal button={"Create Job Category"} header={"Create Job Category"} num={2} /> </div>
                {categoryTable}
           
        </section>
    );
}

export default Portal;
