import { useState } from 'react';
import {
  Button,
  Select as Shivam,
  FormControl,
  FormLabel,
  Input,
  ModalBody,
  ModalFooter
} from '@chakra-ui/react';
import { GroupBase, OptionsOrGroups, Select } from "chakra-react-select";
import { IJobCategoryTable, IPortalTable } from '../../../interfaces/Itable';
import api from '../../../utils/api';

interface CreatePortalProfileProps {
  onClose: () => void;
  portal?: IPortalTable[];
  jobCat?: IJobCategoryTable[];
}

const CreatePortalProfile: React.FC<CreatePortalProfileProps> = ({
  onClose,
  portal,
  jobCat
}: CreatePortalProfileProps) => {
  const [formData, setFormData] = useState({
    portalId: '',
    profileName: '',
    profileLink: '',
    jobCategoryId: ''
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

 
  
  

  const handleCreate = async() => {
  
    console.log('Creating profile:', formData);
    try {
      const res = await api.post(`/portal/create_profile`, formData);
      if (res.data)
        window.location.reload();
    } catch (err) {
      console.log(err)
    }
    onClose();
    
  };

  return (
    <>
      <ModalBody pb={6}>
        <FormControl>
          <FormLabel>Portal name</FormLabel>
          <Shivam
            placeholder='Select option'
            name='portalId'
            onChange={handleChange}
            value={formData.portalId}
          >
            {portal?.map((el) => (
              <option key={el?._id} value={el?._id}>
                {el?.portalName}
              </option>
            ))}
          </Shivam>
        </FormControl>

        <FormControl mt={4}>
          <FormLabel>Profile Name</FormLabel>
          <Input
            placeholder='shivam..'
            name='profileName'
            onChange={handleChange}
            value={formData.profileName}
          />
        </FormControl>

        <FormControl>
          <FormLabel>Profile link</FormLabel>
          <Input
            placeholder='https://...'
            name='profileLink'
            onChange={handleChange}
            value={formData.profileLink}
          />
        </FormControl>
        <FormControl mt={4}>
      <FormLabel>
        Job Category
      </FormLabel>
      <Shivam
            placeholder='Select option'
            name='jobCategoryId'
            onChange={handleChange}
            value={formData.jobCategoryId}
          >
            {jobCat?.map((el) => (
              <option key={el?._id} value={el?._id}>
                {el?.jobName}
              </option>
            ))}
          </Shivam>
    </FormControl>
      
      </ModalBody>
      <ModalFooter>
        <Button colorScheme='blue' mr={3} onClick={handleCreate}>
          Create
        </Button>
        <Button onClick={onClose}>Cancel</Button>
      </ModalFooter>
    </>
  );
};

export default CreatePortalProfile;
