/* eslint-disable no-dupe-keys */
import {
  Box,
  Button,
  FormControl,
  FormLabel,
  Grid,
  Heading,
  Input,
  Select,
  Stack,
  Textarea
} from '@chakra-ui/react';
import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom';
import useDataFetch from '../../hook/useDataFetch';
import { IClientsTable, IPortalProfileTable, IPortalTable } from '../../interfaces/Itable';
import api from '../../utils/api';
import LoginContext from '../../store/loginContext';
import LoadingSpinner from '../UI/loadingSpinner/LoadingSpinner';


interface FormData {
  companyName: string;
  portal: string;
  profile: string;
  jobCategory: string;
  proposalTitle: string;
  proposalLink: string;
  url: string;
  proposalType: string;
  currency: string;
  cost: string;
  estimatedHours: string;
  receivedAmount: string;
  proposalDate: string;
  totalConnects: string;
  proposalDesc: string;
  proposal: string;
  client: string;
  status:string;
}



const CreateProposal = () => {
  const { t } = useTranslation();
  const navigate=useNavigate();
  const {user}=useContext(LoginContext);
  const {id}= useParams();
  const {pathname} = useLocation();
  const PortalUrl = `${process.env.REACT_APP_API_URL}/api/portal`
  const PortalPorfileUrl = `${process.env.REACT_APP_API_URL}/api/portal/profile`
  // const PortalCategoryUrl = `${process.env.REACT_APP_API_URL}/api/portal/category`
  const ClientUrl=`${process.env.REACT_APP_API_URL}/api/portal/clients`

  const [selected, setSelected] = useState<IPortalProfileTable[] | undefined>([])
  const [selectedjob, setSelectedJob] = useState<IPortalProfileTable[] | undefined>([])
  const [loadingid,setLoadingID]=useState(false)
  const { data, error, loading } = useDataFetch<IPortalTable[]>(PortalUrl);
  const { data:clientData, error:clientError, loading:clientLoading } = useDataFetch<IClientsTable[]>(ClientUrl);
  const { data: profileData, error: profileError, loading: profileLoading } = useDataFetch<IPortalProfileTable[]>(PortalPorfileUrl);
  // const { data: categoryData, error: categoryError, loading: categoryLoading } = useDataFetch<IJobCategoryTable[]>(PortalCategoryUrl);
  const [formData, setFormData] = useState<FormData>({
    companyName: '',
    portal: '',
    profile: '',
    jobCategory: '',
    proposalTitle: '',
    proposalLink: '',
    url: '',
    proposalType: '',
    currency: 'usd',
    cost: '',
    estimatedHours: '',
    receivedAmount: '',
    proposalDate: new Date().toISOString().split('T')[0],
    totalConnects: '',
    proposalDesc: '',
    proposal: '',
    client: '',
    status:'new',
   });

   useEffect(() => {
    async function getData() {
      try {
      setLoadingID(true)
        const res = await api.get('/portal/proposal/' + id);
        console.log(res.data);
        setLoadingID(false)
        if(!res.data){
          return
        }
        const {
          companyName,
          portal,
          jobCategory,
          profile,
          proposalTitle,
          proposalLink,
          url,
          proposalType,
          currency,
          cost,
          estimatedHours,
          receivedAmount,
          proposalDate,
          totalConnects,
          proposalDesc,
          proposal,
          client,
          status,
        } = res.data;
        setFormData({
          companyName: companyName || '',
          portal: portal?._id || '',
          profile: profile?._id || '',
          jobCategory:jobCategory?._id ||  '',
          proposalTitle,
          proposalLink,
          url,
          proposalType,
          currency,
          cost,
          estimatedHours,
          receivedAmount,
          proposalDate:new Date(proposalDate).toISOString().split('T')[0]  || new Date().toISOString().split('T')[0], // Set a default value here if it's possibly undefined
          totalConnects,
          proposalDesc,
          proposal,
          client: client?._id || '',
          status,
        });
      } catch (err) {
        console.log(err);
      }
    }
    if(id) getData();
  }, [id]);
  const pathElements = pathname.split('/').filter((el) => el !== '')
  useEffect(() => {
    if (formData["portal"]) {
      let value = profileData?.filter((el) => el.portal._id === formData["portal"]);
      setSelected(value);
    }
    if (formData["profile"]) {
      let value = profileData?.filter((el) => el._id === formData["profile"]);
      setSelectedJob(value)
    }
  }, [formData, profileData])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value
    }));
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    console.log(formData)
    try {
      let url= id?'/portal/update_proposal/'+id :'/portal/create_proposal';
      let creator=user._id;
      const res = id? await api.patch(url,{ ...formData}):await api.post(url,{ ...formData,creator});
      if (res.data) {
        console.log('Client created successfully:', res.data);
        navigate("/proposals")
      }
    } catch (err) {
      console.error('Error creating client:', err);
    }
  };

  const formKeys = Object.keys(formData) as Array<keyof FormData>;
  const formatLabel = (label: string) => {
    return label
      .replace(/([a-z])([A-Z])/g, '$1 $2') // Add space between lowercase and uppercase letters
      .replace(/_/g, ' ') // Replace underscore with space
      .toLowerCase() // Convert to lowercase
      .replace(/^\w/, (c) => c.toUpperCase()); // Capitalize the first letter
  };

  const handleClick = (index:number) => {
    const newPath = `/${pathElements.slice(0, index + 1).join('/')}`; // Build the new path
    navigate(newPath); // Navigate to the new path
  };



  return (
    <>
    {loadingid ? <LoadingSpinner/>:<>
      <Box mb={10} mt={-5}>
      {pathElements.slice(0,1).map((element, index) => (
          <span key={index} style={{color:"black"}}>
            <Link to={`/${pathElements.slice(0, index + 1).join('/')}`} onClick={() => handleClick(index)}>
              {element}
            </Link>
            {index !== pathElements.length - 1 && ' / '}
          </span>
        ))}
        <Heading color='red'>{id?'Edit Proposal':'Create Proposal'}</Heading>
      </Box>
      <form onSubmit={handleSubmit}>
        <Grid templateColumns="repeat(2, 1fr)" gap={6}>
          {formKeys.map((key) => {
            if (key === 'portal') {
              return (
                <FormControl key={key} isRequired>
                  <FormLabel>{formatLabel(key)}</FormLabel>
                  <Select required={true}
                    name={key}
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Select ${key}`}
                  >
                    {!loading && data && data.map((portal) => (
                      <option key={portal._id} value={portal._id}>
                        {portal.portalName}
                      </option>
                    ))}
                  </Select>
                </FormControl>
              );
            } else if (key === 'profile') {
              return (
                <FormControl key={key} isRequired={true}>
                  <FormLabel>{formatLabel(key)}</FormLabel>
                  <Select
                    name={key}
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Select ${key}`}
                  >
                    {selected && selected.map(
                      (profile) => (
                        <option key={profile._id} value={profile._id}>
                          {profile.profileName}
                        </option>
                      )
                    )}
                  </Select>
                </FormControl>
              );
            } else if (key === 'jobCategory') {
              return (
                <FormControl key={key} isRequired={true}>
                  <FormLabel>{formatLabel(key)}</FormLabel>
                  <Select
                    name={key}
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Select ${formatLabel(key)}`}
                  >
                    {selectedjob && selectedjob.map(
                      (profile) => (
                        <option key={profile.jobCategory._id} value={profile.jobCategory._id}>
                          {profile.jobCategory.jobName}
                        </option>
                      )
                    )}
                  </Select>
                </FormControl>
              );
            } else if (key === 'proposalType') {
              return (
                <FormControl key={key} isRequired>
                  <FormLabel>{formatLabel(key)}</FormLabel>
                  <Select
                    name={key}
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Select ${formatLabel(key)}`}
                  >
                    {["hourly", "fixed"].map((category) => (
                      <option key={category} value={category}>
                        {category}
                      </option>
                    ))}
                  </Select>
                </FormControl>
              );
            } else if (key === 'client') {
              return (
                <FormControl key={key} isRequired={true}>
                  <FormLabel>{formatLabel(key)}</FormLabel>
                  <Select
                    name={key}
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Select ${formatLabel(key)}`}
                  >
                    {clientData && clientData.map((client) => (
                      <option key={client._id} value={client?._id}>
                        {client.clientName}
                      </option>
                    ))}
                  </Select>
                </FormControl>
              );
            } else if (key === 'proposalDesc') {
              return (
                <FormControl key={key}>
                  <FormLabel>{formatLabel(key)}</FormLabel>
                  <Textarea  name={key}
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Enter ${formatLabel(key)}`}></Textarea>
                </FormControl>
              );
            } else if (key === 'proposal') {
              return (
                <FormControl key={key}>
                  <FormLabel>{formatLabel(key)}</FormLabel>
                  <Textarea  name={key}
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Enter ${formatLabel(key)}`}></Textarea>
                </FormControl>
              );
            } 
            else if (key === 'status') {
              return (
            <FormControl key={key}>
            <FormLabel>Status</FormLabel>
            <Select
              placeholder='Select status'
              name={key}
              onChange={handleChange}
              value={formData[key]}
            >
              {["new", "discussion", "hired", "completed", "rejected", "follow_up", "pause", "spam"].map(status => (
                <option key={status} value={status}>{status}</option>
              ))}
            </Select>
          </FormControl>
              )
               }
            else if (key === 'proposalDate') {
              return (
            <FormControl key={key}>
            <FormLabel>{`${formatLabel(key)}`}</FormLabel>
            <Input
                    name={key}
                    type='date'
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Enter ${formatLabel(key)}`}
                  />
          </FormControl>
              )
               }
               
               
               
               else {
              return (
                <FormControl key={key} isRequired={key==="companyName" ? true :key==="proposalTitle" ? true :key==="totalConnects" ? true : key==="cost" ? true : false}>
                  <FormLabel>{formatLabel(key)}</FormLabel>
                  <Input
                    name={key}
                    value={formData[key]}
                    onChange={handleChange}
                    placeholder={`Enter ${formatLabel(key)}`}
                  />
                </FormControl>
              );
            }
          })}
        </Grid>
<Box mt={10}>
  <Stack direction='row' justify={'flex-end'} spacing={10}>
        <Button colorScheme="blue" type="submit">
          {t('upload')}
        </Button>
        <Button onClick={()=> navigate("/proposals")}>{t('cancel')}</Button></Stack>
        </Box>
      </form></>}
    </>

  );
};

export default CreateProposal;
