import React, { useState } from 'react';
import {
  FormControl,
  FormLabel,
  Input,
  ModalBody,
  ModalFooter,
  Button
} from '@chakra-ui/react';
import api from '../../../utils/api';

interface CreateJobCatProps {
  onClose: () => void;
}

const CreateJobCat: React.FC<CreateJobCatProps> = ({ onClose }: CreateJobCatProps) => {
  const [formData, setFormData] = useState({
    jobName: '',
    desc: ''
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const createJobCategory = async() => {
    console.log('Creating job category:', formData);
    try {
      const res = await api.post(`/portal/create_category`, formData);
      if (res.data)
        window.location.reload();
    } catch (err) {
      console.log(err)
    }
    onClose();
  };

  return (
    <>
      <ModalBody pb={6}>
        <FormControl>
          <FormLabel>Job Category name</FormLabel>
          <Input
            placeholder='Mern,Desiging,Php..'
            name='jobName'
            onChange={handleChange}
            value={formData.jobName}
          />
        </FormControl>

        <FormControl mt={4}>
          <FormLabel>Desc</FormLabel>
          <Input
            placeholder='desc'
            name='desc'
            onChange={handleChange}
            value={formData.desc}
          />
        </FormControl>
      </ModalBody>

      <ModalFooter>
        <Button colorScheme='blue' mr={3} onClick={createJobCategory}>
          Create
        </Button>
        <Button onClick={onClose}>Cancel</Button>
      </ModalFooter>
    </>
  );
};

export default CreateJobCat;
